
.c-donations-types {
  max-width: $s-page-wrap--xlarge;
  margin: 0 auto;
  
  &__inner {
    padding: 4.3125rem $s-gutter--edge-mobile 2.0625rem;
  }

  &__title {
    text-align: center;
    width: 71%;
    margin: 0 auto 3.25rem;
    max-width: 40.625rem;
  }

  &__types-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2.125rem;
    grid-column-gap: 4.0625rem;
  }



  &__type-container {
    position: relative;
    display: grid;
    justify-content: center;
    align-items: center;
    justify-items: center;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;

    &:nth-child(odd) {
      justify-self: end;
    }

    &:nth-child(even) {
      justify-self: start;
    }

    &:last-child {
      justify-self: center;
      grid-column: span 2;
    }

    .c-donations-types__icon-container svg:last-of-type {
      width: 50%;
      z-index: 2;
      @include t-absolute-center-x-y();
    }


    &:nth-of-type(1) .c-donations-types__blob path {
      fill: #DA8356;
    }

    &:nth-of-type(2) .c-donations-types__blob path {
      fill: #CB95A5;
    }

    &:nth-of-type(3) .c-donations-types__blob path {
      fill: #DD6D62;
    }

    &:nth-of-type(4) .c-donations-types__blob path {
      fill: #88AA92;
    }

    &:nth-of-type(5) .c-donations-types__blob path {
      fill: #81ADC6;
    }

    &:nth-of-type(6) .c-donations-types__blob path {
      fill: #FDCF5e;
    }

    &:nth-of-type(7) .c-donations-types__blob path {
      fill: #F6A898;
    }
  }

  &__icon-container {
    position: relative;
    width: 6.75rem;
  }

  &__blob {
    height: auto;
    grid-row: 1/-1;
    grid-column: 1/-1;
    width: 6.75rem;
  }

  &__label {
    text-align: center;
    margin-top: 0.4375rem;
  }



  @include t-media-query($s-small-up) {


    &__type-container {
      &:last-child {
        grid-column: span 2;
      }
    }
  }


  @include t-media-query($s-medium-up) {
    &__inner {
      padding: 8.3125rem 6% 8rem;
    }

    &__title {
      @include t-font--heading-4-laptop;
      margin-bottom: 5.875rem;
    }

    &__blob {
      width: 14.625rem;
      height: 7.5rem;
    }

    &__icon-container {
      width: unset;
    }

    &__types-wrapper {
      grid-template-columns: 1fr 1fr 1fr;
      max-width: 62.5rem;
      margin: 0 auto;
    }


    &__type-container {
      gap: 1.25rem;

      .c-donations-types__icon-container svg:last-of-type {
        width: 29%;
      }
      &:nth-child(odd) {
        justify-self: initial;
      }

      &:nth-child(even) {
        justify-self: initial;
      }

      &:last-child {
        justify-self: center;
        grid-column: span 3;
      }
    }
  }


  @include t-media-query($s-xlarge-up) {
    &__types-wrapper {
      display: flex;
      gap: 1%;
      max-width: unset;
      justify-content: center;
    }

    &__type-container {

      &:last-child {
        justify-self: initial;
        grid-column: span 1;
      }
    }

    &__icon-container {
      display: flex;
      justify-content: center;
    }

    &__blob {
      width: 65%;
      height: 100%;
    }

    &__type-container{
      gap: .875rem;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__inner {
      padding-top: 10.8125rem;
      padding-bottom: 12.25rem;
      max-width: $s-page-wrap--xlarge;
      margin: 0 auto
    }

    &__title {
      @include t-font--heading-4-desktop;
      width: 38%;
      line-height: 1.4;
    }
  }
}